<template>
    <div class="info">
        <h1 style=" color: rgba(220, 228, 220, 1)">项目经验</h1>
        <transition :duration="{ enter: 2500, leave: 1500 }" enter-active-class="animated fadeInRight">
            <div class="cont" v-if="show">
                <el-carousel :autoplay="false" arrow="always" height="500px">
                    <el-carousel-item v-for="item in profiles" :key="item">
                        <h2 v-text="item.name"></h2>
                        <p v-text="'技术栈: ' + item.skills"></p>
                        <h1 style="text-align: center;font-size: 18px">项目介绍</h1>
                        <p v-html="item.content" style="text-align: left;"></p>
                        <h1 style="text-align: center;font-size: 18px">我的工作</h1>
                        <p v-html="item.mySkills" style="text-align: left;"></p>
                        <!-- <p v-text="'github地址: ' + item.github"></p> -->
                    </el-carousel-item>
                </el-carousel>
            </div>
        </transition>
    </div>
</template>
<script>
export default {
  data() {
    return {
      show: false,
      profiles: [
        {
          name: "国家电网库房及任务管理系统",
          skills:
            "vue vue-cli vue-router bootstrap axios idea echarts leaflet mock svn Nginx",
          content:
            "1.库房管理系统是一个对带电作业任务过程、作业班组及人员、作业使用工具的智能管理系统。对带电作业任务下发班组，根据任务类型推荐工具，记录特殊作业车辆运行路径，查看上传的带电作业过程视频。管控库房内部温湿度，根据投运时间、使用次数提醒作业工具是否需要检验或报废。统计各班组/个人进行任务的情况及时段。录入新购的设备及新入职的从业人员。<br/>" +
            "2.该项目采用前后端分离开发，因为数据关联方式复杂，提前进行数据库设计及接口预设计，前后端开发人员根据接口文档进行开发并在开发完成后直接对接并调整。<br/>" +
            "3.该项目部署在眉山、绵阳及德阳的内网服务器上，无法请求到外网地图接口，需要切割瓦片图并部署到内网服务器上，达到展示地图的目的。<br/>" +
            "4.项目框架用的是Bootstrap，底层为vue.js，在Bootstrap的基础上进行了二次开发，封装了很多通用的组件，例如消息通知弹窗、表格切换等等。<br />" +
            "5.项目的代码管理工具使用的是SVN。<br />",
          mySkills:
            "1:整理电网工作人员提出的需求，编写用户故事和用例，转化成原型图供数据库及接口设计，并用于甲方功能评审。<br/>" +
            "2:使用vue-cli及webpack搭建前端框架，引入bootstroop ui并进行页面开发。<br/>" +
            "3:在后端接口上线前使用mock.js拦截请求，根据接口设计模拟数据进行页面渲染调试。<br/>" +
            "4:切割瓦片图并部署到内网服务器，使用leaflet.js完成地图功能开发。<br/>" +
            "5:熟练掌握大中型vue项目模块式开发，提高开发效率。<br/>" +
            "6:将代码部署到服务器上并进行数据备份。"
        },
        {
          name: "安心养老、安心助老APP",
          skills: "vue vue-cli vue-router vuex axios idea mint-ui mock git python",
          content:
            "1.该项目针对老年用户及养老从业人员，是一个养老机构、养老年金、老人、养老从业人员一站式平台，旨在向老人展示养老政策信息及推荐的养老服务，帮助签约民政局向老人发放养老年金，引导养老从业人员快速完成老人订购的服务，推广优质养老机构并在线上实现预定机构。<br />" +
            "2.使用基于Vue二次封装的PJF框架及mint-ui进行开发。<br />" +
            "3.使用webview技术将html页面嵌入到IOS及Android的APP内部。<br />" +
            "4.跨平台app开发框架，封装了app需要的大部分组件并归库，可二次开发。 <br />",
          mySkills:
            "1.封装与IOS和Android壳上的通讯方法，通过注册Vue全局广播的方式监听异步回调。<br />" +
            "2.封装带搜索框、历史纪录、首字母定位的城市选择组件，将城市代码与名称上报至父组件，完成组件间通讯。该组件经过优化后可直接用于其他项目。<br />" +
            "3.封装瀑布流无感加载组件，在HTML页面中实现无感加载，及返回锚点定位，做到与原生APP近乎相同的体验<br />" +
            "4.建立Vuex状态管理，将部分非业务逻辑放在前端进行并储存，提高切换页面速度。<br />" +
            "5.开发python脚本比对excel、word及txt同类型文件中的差异并检索关键数据"
        },
        {
          name: "安心养老微信小程序",
          skills: "wxss wxml 小程序云开发",
          content:
            "微信小程序拥有无需下载，即开即用，用完即走的功能，能显著增加用户粘性及用户数量。该项目是安心养老APP的微信版本，将安心养老APP的常用功能移植到微信小程序上。",
          mySkills:
            "1.调研微信小程序开发方式，对比wepy、mpvue及原生开发优劣势，进行技术选型，确定并搭建框架。<br />" +
            "2.实现小程序分包，将不同类型的功能模块拆分到其他包中，将资源分散加载，提高打开小程序并进入首页的速度。<br />" +
            "3.调研微信小程序支付分账方式，实现小程序支付功能开发。"
        },
        {
          name: "电子科技大学校友卡申领系统",
          skills: "html javascript css svn amaze-ui java jsp",
          content:
            "为电子科技大学开发一个接入微信公众号的项目，为电子科技大学毕业生，曾在电子科大工作的后勤人员、教师提供一个可以申领校友卡的平台以及配套的校友卡申领记录审核平台。电子科持校友卡可进入电子科技大学图书馆、体育馆等地，并可在校园超市、食堂中进行消费。",
          mySkills:
            "1.与电子科技大学校友会工作人员讨论需求并绘制原型图，进行接口设计和数据库设计。<br />" +
            "2.完成校友卡申领系统及后台管理系统开发。<br />" +
            "3.使用EL表达式进行数据渲染。<br />" +
            "4.完成前端开发后在java框架中配置controller层和service层完成页面部署。 <br />" +
            "5.通过MySQL定期清理数据库。"
        },
        {
          name: "黑格珠宝线上推广平台及后台系统",
          skills: "html javascript css vue php bootstrap wxss wxml 小程序云开发",
          content:
            "该项目为黑格珠宝商家提供一个可以上传珠宝图片、参数的平台；让线上推广人员能通过微信小程序获取上传的商品信息，并制作自定义海报、生成图片并保存到本机、发布到朋友圈等功能开发，让推广人员能高度自由地设计海报进行推广从而提高销量。",
          mySkills:
            "1.与黑格珠宝工作人员讨论需求并绘制原型图<br />" +
            "2.完成html代码绘制成canvas画布、并将canvas画布内容转换橙jpeg、png等指定类型的图片<br />" +
            "3.封装可复用的多图上传组件。 <br />" +
            "4.将后台管理系统通过PHP切片的方式发布到公网。"
        }
      ]
    };
  },
  methods: {
    timeout() {
      setTimeout(() => {
        this.show = true;
      }, 1000);
    }
  },
  mounted() {
    this.timeout();
  }
};
</script>

<style lang="less" scoped>
@import "../less/index.less";
.info {
  .info;
  .cont {
    .cont;
    width: 90%;
    height: 80%;
    .item {
      background-color: rgba(59, 123, 13, 0.2);
      padding: 10px;
      border-radius: 15px;
      margin: 10px auto;
      @media screen and (max-width: 768px) {
        padding: 5px;
        margin: 5px auto;
      }
      &:hover {
        border-radius: 25px;
        background-color: rgba(10, 135, 87, 0.3);
      }
      p {
        text-align: left;
        text-indent: 28px;
        font-size: 1.1rem;
        line-height: 1.2;
        @media screen and (max-width: 768px) {
          text-indent: 12px;
          font-size: 0.7rem;
          line-height: 1.1;
        }
      }
    }
  }
}
.el-carousel__item h3 {
  // color: #475669;
  font-size: 15px;
  // opacity: 0.75;
  line-height: 600px;
  margin: 0;
}
.el-carousel__item{
  padding: 0 60px;
  width: calc(100% - 120px);
}
.el-carousel__item:nth-child(n) {
  background-color: rgba(241, 233, 233, 0.904);
}
.el-carousel__button{
  background: #0F0 !important;
}
// .el-carousel__item:nth-child(2n + 1) {
//   background-color: #d3dce6;
// }
</style>



